.main {
    display: flex;
    flex-direction: column;
    border: 1px solid #80A5C2;
    border-radius: 5px;
    height: 600px;
    padding: 1rem;
    background-color: #80a5c277;
    width: 400px;
}

.main-form {
    height: 33%;
}

.to-do-list-container {
    height: 100%;
    padding: 1rem;
    background-color: #C2B56F;
    color: #fff;
    overflow: auto;
    margin-bottom: 20px;
}

.to-do-list-container::-webkit-scrollbar {
    width: 10px;
    background-color: #fff;
}

.to-do-list-container::-webkit-scrollbar-thumb {
    background: #6B2116;
}

.to-do-list-container::-webkit-scrollbar-thumb:hover {
    background: rgb(136, 70, 70);
}

.to-do {
    border: 2px solid #000;
    padding: 0.5rem;
    margin: 40px 0;
    letter-spacing: 2px;
    max-width: 100%;
    background-color: #6B2116;
    box-shadow: 0px 0px 5px #000;
    height: 70px;
}

.to-do i {
    cursor: pointer;
    color: #979084;
    margin: 0 5px;
}

.to-do i:hover {
    opacity: .7;
}

.to-do-text {
    max-width: 300px;
    display: block;
    margin-bottom: 15px;
    word-break: break-all;
}

.main-footer {
    height: 33%;
}

@media screen and (max-width: 768px) {
    .main {
        background: transparent;
        border: none;
        padding: 0;
        width: 300px;
    }
}

@media screen and (max-height: 600px) {
    .main {
        height: 500px;
    }
}