@font-face {
    font-family: 'MedievalSharp Regular';
    font-style: normal;
    font-weight: normal;
    src: local('MedievalSharp Regular'), url('../Containers/MedievalSharp.woff') format('woff');
}

* {
    font-family: "MedievalSharp Regular";
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: url("../../src/Images/background.webp");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

ul {
    list-style: none;
    padding: 0;
}

input {
    border: 0;
    outline: 0;
    padding: .5rem;
    border-radius: 5px;
    letter-spacing: 2px;
    font-weight: 700;
    width: 75%;
}

input:placeholder-shown {
    letter-spacing: 1px;
}

button {
    border: 0;
    outline: 0;
    padding: 0.5rem;
    border-radius: 5px;
    cursor: pointer;
    font-weight: 700;
}

button:hover {
    opacity: .9;
}

button:active {
    transform: scale(.97);
}